.pokemon-details{
  padding-top: 10px;
  animation: slide 0.6s ease-out;
}

@keyframes slide {
  0% {
    opacity: 0;
    margin-bottom: -50px;
  }
  50%{
    opacity: 0;
    margin-bottom: 0px;
    margin-right:-200px;
    
  }
  
  100% {
    opacity: 1;
    
    margin-right: 0px;
  }
  
}